.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: $buttonHeight;
  text-decoration: none;
  color: #1d1d1d;
  font-weight: 700;
  font-size: 16px;
  line-height: 169%;
  text-align: center;
  background-color: #fff;
  border-radius: 40px;
  border: 0px;
  transition: box-shadow $transitionDuration;
  cursor: pointer;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
